.title-container {
  position: relative;
  margin-bottom: 10px;
  display: inline-block; }
  .title-container .title {
    font-size: 24px; }
    @media (max-width: 599px) {
      .title-container .title {
        font-size: 24px; } }
  .title-container .background {
    position: absolute;
    top: 2px;
    right: 0;
    width: 100%;
    height: 30px;
    background: rgba(3, 174, 239, 0.3);
    transform: skew(20deg); }

.attribution {
  font-size: 12px;
  color: #666;
  margin-bottom: 20px; }

.description {
  font-size: 16px;
  line-height: 24px; }
  .description p:not(:last-child) {
    margin-bottom: 20px; }
  @media (max-width: 599px) {
    .description {
      font-size: 14px;
      line-height: 22px; } }

.image-list {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center; }
  @media (min-width: 600px) {
    .image-list {
      flex-direction: row;
      flex-wrap: wrap; } }
  .image-list img {
    width: 300px;
    margin-bottom: 30px;
    box-shadow: 10px 10px 20px #333333;
    cursor: zoom-in; }
    @media (min-width: 600px) {
      .image-list img {
        width: 45%; } }

.lightbox-image-container {
  width: 50%;
  padding: 20px;
  display: flex;
  justify-content: center; }
  @media (max-width: 599px) {
    .lightbox-image-container {
      width: 100%; } }
  .lightbox-image-container img {
    width: 100%; }
  .lightbox-image-container .lightbox-image {
    width: 100%;
    height: 100%; }
