.container {
  min-height: 100vh; }

.inner {
  max-width: 1100px;
  margin: 0 auto; }
  .inner.padding {
    padding: 30px 20px; }
    @media (min-width: 600px) {
      .inner.padding {
        padding: 60px 30px; } }

.columns {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-content: flex-start; }
  @media (min-width: 600px) {
    .columns {
      flex-direction: row; } }
  .columns .left-column {
    margin-right: 40px; }
    @media (max-width: 599px) {
      .columns .left-column {
        width: 100% !important;
        text-align: center;
        margin-bottom: 30px;
        padding-bottom: 20px;
        border-bottom: 1px solid #aaa; } }
  @media (max-width: 599px) {
    .columns .right-column {
      width: 100% !important; } }
